import { Link as RouterLink } from 'react-router-dom';

// material-ui

import { Grid } from '@material-ui/core';

// project imports
import AuthWrapper from '../AuthWrapper';
import Logo from "../../../../ui-component/Logo";
import AuthCardWrapper from "../AuthCardWrapper";
import AWSLogin from './AWSLogin';

const Login = () => (
    <AuthWrapper>
      <Grid
	container
	direction="column"
	justifyContent="flex-end"
	sx={{ minHeight: '100vh' }}
      >
        <Grid item xs={12}>
          <Grid
	container
	justifyContent="center"
	alignItems="center"
	sx={{ minHeight: '100vh' }}
          >
            <Grid item sx={{ m: { xs: 1, sm: 3 }, mb: 0 }}>
              <AuthCardWrapper>
                <Grid
	container
	spacing={2}
	alignItems="center"
	justifyContent="center"
                >
                  <Grid item>
                    <RouterLink to="#">
                      <Logo />
                    </RouterLink>
                  </Grid>
                  <Grid item xs={12}>
                    <AWSLogin />
                  </Grid>
                </Grid>
              </AuthCardWrapper>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </AuthWrapper>
  );

export default Login;
